import React, { useState, useEffect } from "react";
import { Switch, Route } from "react-router-dom";
import Home from "./Home/Home";
import About from "./About/About";
import RecipesList from "./RecipeListing/RecipesList";
import AddData from "./AddNew/AddData";
import InstructionsCard from "./RecipeListing/InstructionsCard";
import Nav from "react-bootstrap/Nav";
import axios from "axios";
import Popup from "./AddNew/Popup";
// import { showPopup } from "./AddNew/AddData";

const Landing = () => {
  return (
    <div>
      <Home />
    </div>
  );
};

const Info = () => {
  return (
    <Nav>
      <About />
    </Nav>
  );
};

const Recipes = () => {
  return (
    <Nav>
      <RecipesList />
    </Nav>
  );
};

const Main = () => {
  return (
    <div>
      <Switch>
        <Route path="/" exact component={Landing} />
        <Route path="/recipes" component={Recipes}>
          <RecipesList />
        </Route>
        <Route path="/about" component={Info} />
        <Route path="/add">
          <div className="newRecipePage">
            <AddData />
            {/* {showPopup && <Popup {...newRecipe} />} */}
          </div>
        </Route>
        <Route path="/recipes/:id">
          <InstructionsCard />
          <p>Recipe here</p>
        </Route>
      </Switch>
    </div>
  );
};

export default Main;
